.m-tabarea .m-tabct, .m-tabct .tabctitem {
    width: 100%;
    height: 100%;
}

.m-tabarea .m-tabct {
    display: block;
}
.m-hmhot .hotop {
    position: relative;
    padding-top: 38.9%;
    overflow: hidden;
    background: url(https://xuyouwg.github.io/react-demo/images/hot_music_bg.jpg) no-repeat;
    background-size: contain;
}
.m-hmhot .hotopct {
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    -webkit-box-pack: center;
    justify-content: center;
    z-index: 2;
    padding-left: 20px;
    box-sizing: border-box;
}
.m-hmhot .hotop:after, .m-hmhot .hotopct {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
.m-hmhot .hoticon {
    width: 142px;
    height: 67px;
    background-position: -24px -30px;
}
.u-hmsprt {
    background: url(https://xuyouwg.github.io/react-demo/images/index_icon.png) no-repeat;
    background-size: 166px 97px;
}
.m-hmhot .hottime {
    margin-top: 10px;
    color: hsla(0,0%,100%,.8);
    font-size: 12px;
    transform: scale(.91);
    transform-origin: left top;
}
.m-hmhot .hotop:after {
    content: " ";
    z-index: 1;
    background-color: rgba(0,0,0,.2);
}
/* 热歌榜内容 */
.m-hmhot .hotcont {
    position: relative;
}
.m-sgitem, .m-sgitem .sgfl {
    display: flex;
}
.m-sgitem {
    padding-left: 10px;
}
.m-sgitem .sgfl-cred {
    color: #df3436;
}

.m-sgitem .sgfl {
    -webkit-box-align: center;
    align-items: center;
    width: 28px;
    font-size: 17px;
}
.m-sgitem .sgchfl, .m-sgitem .sgfr {
    -webkit-box-flex: 1;
    flex: 1 1 auto;
}

.m-sgitem .sgfr {
    display: flex;
    position: relative;
}
.m-sgitem .sgchfl {
    padding: 6px 0;
    width: 0;
}
.m-sgitem .sgtl {
    font-size: 17px;
}

.f-thide {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.m-sgitem .sginfo {
    font-size: 12px;
    color: #888;
}
.f-thide {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.m-sgitem .sghot {
    display: inline-block;
    width: 12px;
    height: 8px;
    margin-right: 4px;
}
.u-hmsprt {
    background: url(https://xuyouwg.github.io/react-demo/images/index_icon.png) no-repeat;
    background-size: 166px 97px;
}
.m-sgitem .sgchfr {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding: 0 10px;
}
.m-sgitem .sgchply {
    display: inline-block;
    width: 22px;
    height: 22px;
    background-position: -24px 0;
}
.u-hmsprt {
    background: url(https://xuyouwg.github.io/react-demo/images/index_icon.png) no-repeat;
    background-size: 166px 97px;
}
.f-bd:after {
    position: absolute;
    z-index: 2;
    content: "";
    top: 0;
    left: 0;
    pointer-events: none;
    box-sizing: border-box;
    width: 300%;
    height: 300%;
    transform: scale(.333333);
    transform-origin: top left;
    border: 0 solid rgba(0,0,0,.1);
}
.m-sgitem .sgalia {
    color: #888;
    margin-left: 4px;
}
.m-sgitem:nth-child(1)>.sgfl,.m-sgitem:nth-child(2)>.sgfl,.m-sgitem:nth-child(3)>.sgfl{
    color: #df3436;
}

/* 热歌榜底部 */
.m-hmhot .hotdn {
    height: 55px;
    line-height: 55px;
    text-align: center;
}
.m-hmhot .hotview {
    display: inline-block;
    color: #999;
    padding-right: 14px;
    background: url(https://xuyouwg.github.io/react-demo/images/hotview.svg) 100% no-repeat;
    background-size: 7px 12px;
}