body {
  margin: 0;
  padding: 0;
  list-style: none;
  font: 14px/1.5 Helvetica,sans-serif;
  color: #333;
}
a, a:visited {
  text-decoration: none;
  color: #333;
}
a, button {
  cursor: pointer;
}
nav {
  border-bottom:1px solid rgba(0,0,0,.08);
}


/* 头部logo */
.m-hometop {
    height: 64px;
}
.m-hometop .topfix {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    width: 100%;
    height: 64px;
    background-color: #d43c33;
    box-sizing: border-box;
}
.m-hometop .topfl {
    position: relative;
    width: 142px;
    height: 25px;
}
.m-hometop .topplh {
    opacity: 0;
    color: #d43c33;
}
.m-hometop .topsvg {
    position: absolute;
    left: 0;
    top: 0;
    width: 142px;
    height: 25px;
}
.m-hometop .topbton {
    position: relative;
    display: inline-block;
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    color: #fff;
    font-size: 15px;
    border:  1px solid rgba(245, 237, 237, 0.3);
    border-radius: 37.5%/100%;
}
  
/* 导航条 */
.u-tab {
    display: flex;
    position: fixed;
    top: 64px;
    left: 0;
    z-index: 100;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    background: #fff;
}
.u-tab .tabtitle {
    flex: 1 1;
    width: 33.3%;
    height: 100%;
    text-align: center;
    cursor: pointer;
}

.u-tab  .tabtxt .z-selected{
    color: #d33a31;
}
.u-tab .tabtxt {
    position: relative;
    display: inline-block;
    height: 100%;
    padding: 0 5px;
    box-sizing: border-box;
    color: #333;
    font-size: 15px;
    line-height: 40px;
}
.u-tab .tabtxt em, .u-tab .tabtxt span {
    vertical-align: middle;
}
.m-tabarea-index .u-tab:after {
    border-color: #ccc;
    z-index: 0;
}
.u-tab .tabtxt .z-selected:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -1px;
    z-index: 3;
    width: 100%;
    height: 4px;
    -webkit-transform: scaleY(.5);
    transform: scaleY(.5);
    background: #d33a31;
}
.f-bd-btm:after {
    border-bottom-width: 1px;
}
.f-bd:after {
    position: absolute;
    z-index: 2;
    content: "";
    top: 0;
    left: 0;
    pointer-events: none;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    -webkit-transform-origin: top left;
    transform-origin: top left;
    border: 0 solid rgba(0,0,0,.1);
}
a{
    text-decoration: none;
    color:#333;
}
nav {
    border-bottom:1px solid rgba(0,0,0,.08);
}


