    width: 13px;
    height: 13px;
    background-image: url(https://xuyouwg.github.io/react-demo/images/search.svg);
}
.m-input .input {
    width: 100%;
    height: 30px;
    line-height: 18px;
    background: transparent;
    font-size: 14px;
    color: #333;
}
input {
    -webkit-appearance: none;
    border-radius: 0;
    border: 0;
    outline: none;
    font: 14px/1.5 Helvetica,sans-serif;
}
.m-input .holder {
    position: absolute;
    left: 30px;
    top: 5px;
    font-size: 14px;
    color: #c9c9c9;
    background: transparent;
    pointer-events: none;
}
.m-input .close {
    margin: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 30px;
    height: 30px;
    line-height: 28px;
    text-align: center;
}
.m-input .u-svg-empty {
    display: none;
    vertical-align: middle;
}
.u-svg {
    display: inline-block;
    vertical-align: middle;
    background-position: 0 0;
    background-size: contain;
    background-repeat: no-repeat;
}
.u-svg-empty {
    width: 14px;
    height: 14px;
    background-image: url(https://xuyouwg.github.io/react-demo/images/close.svg);
}
.m-input:after {
    border-color: rgba(0,0,0,.1);
}
