.tabctitem {
    width: 100%;
    height: 100%;
    position: relative;
    top:38px
  }
  .m-homeremd {
    padding-top: 20px;
  }
  .m-homeremd .remd_tl {
    position: relative;
    padding-left: 9px;
    margin-bottom: 14px;
    font-size: 17px;
    height: 20px;
    line-height: 20px;
  }
  .m-homeremd .remd_tl:after {
    content: " ";
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -9px;
    width: 2px;
    height: 16px;
    background-color: #d33a31;
  }
  .m-homeremd .remd_songs {
    position: relative;
    padding-bottom: 24px;
  }
  .m-homeremd .remd_li:first-child {
    padding-left: 0;
    padding-right: 2px;
  }
  .m-homeremd .remd_li {
    display: block;
    float: left;
    width: 33.3%;
    padding-left: 1px;
    padding-right: 1px;
    padding-bottom: 16px;
    box-sizing: border-box;
  }
  .m-homeremd .remd_ul:after {
    display: block;
    visibility: hidden;
    clear: both;
    height: 0;
    overflow: hidden;
    content: ".";
  }
  .m-homeremd .remd_li:first-child {
    padding-left: 0;
    padding-right: 2px;
  }
  .m-homeremd .remd_img {
    position: relative;
    padding-bottom: 100%;
  }
  .m-homeremd .remd_img>.u-img {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 1;
  }
  .m-homeremd .remd_lnum {
    position: absolute;
    right: 5px;
    top: 2px;
    z-index: 3;
    padding-left: 13px;
    color: #fff;
    font-size: 12px;
    background-position: 0;
    background-repeat: no-repeat;
    background-size: 11px 10px;
    text-shadow: 1px 0 0 rgba(0,0,0,.15);
  }
  .u-earp {
    background-image: url(https://xuyouwg.github.io/react-demo/images/下载.svg);
  }
  .m-homeremd .remd_img:after {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 20px;
    z-index: 2;
    background-image: -webkit-gradient(linear,left top,left bottom,from(rgba(0,0,0,.2)),to(transparent));
    background-image: -webkit-linear-gradient(top,rgba(0,0,0,.2),transparent);
    background-image: linear-gradient(180deg,rgba(0,0,0,.2),transparent);
  }
  .m-homeremd .remd_text {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      height: 30px;
      overflow: hidden;
      padding: 6px 2px 0 6px;
      line-height: 1.2;
      font-size: 13px;
  }
  
