.m-hmsrch {
    background: #fbfcfd;
    position: relative;
    top: 38px;
}
.m-input {
    padding: 15px 10px;
}
.m-input .inputcover {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    box-sizing: border-box;
    background: #ebecec;
    border-radius: 30px;
}
.m-input .u-svg-srch {
    position: absolute;
    left: 0;
    top: 9px;
    margin: 0 8px;
    vertical-align: middle;
}
.u-svg-srch {
    width: 13px;
    height: 13px;
    background-image: url(https://xuyouwg.github.io/react-demo/images/search.svg);
}
.m-input .input {
    width: 100%;
    height: 30px;
    line-height: 18px;
    background: transparent;
    font-size: 14px;
    color: #333;
}
input {
    -webkit-appearance: none;
    border-radius: 0;
    border: 0;
    outline: none;
    font: 14px/1.5 Helvetica,sans-serif;
}
.m-input .holder {
    position: absolute;
    left: 30px;
    top: 5px;
    font-size: 14px;
    color: #c9c9c9;
    background: transparent;
    pointer-events: none;
}
.m-input .close {
    margin: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 30px;
    height: 30px;
    line-height: 28px;
    text-align: center;
}
.m-input .u-svg-empty {
    display: none;
    vertical-align: middle;
}
.u-svg {
    display: inline-block;
    vertical-align: middle;
    background-position: 0 0;
    background-size: contain;
    background-repeat: no-repeat;
}
.u-svg-empty {
    width: 14px;
    height: 14px;
    background-image: url(https://xuyouwg.github.io/react-demo/images/close.svg);
}
.m-input:after {
    border-color: rgba(0,0,0,.1);
}
.m-hotlist {
    padding: 15px 10px 0;
}
.m-hotlist .title {
    margin: 0;
    font-size: 12px;
    font-weight: normal;
    line-height: 12px;
    color: #666;
}
.m-hotlist .list {
    margin: 10px 0 7px;
    padding: 0;
}
.m-hotlist .item {
    display: inline-block;
    height: 32px;
    margin-right: 8px;
    margin-bottom: 8px;
    padding: 0 14px;
    font-size: 14px;
    line-height: 32px;
    color: #333;
}
.m-hotlist .item:after {
    border-color: #d3d4da;
    border-radius: 60px;
}

.m-recom {
    margin: 0 auto;
    display: none;
}
.m-recom .title {
    height: 50px;
    margin-left: 10px;
    padding-right: 10px;
    font-size: 15px;
    line-height: 50px;
    color: #507daf;
}
ul,li,h3{
    padding: 0;
    margin: 0
}
.m-recom .recomitem {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    height: 45px;
    padding-left: 10px;
}
.m-recom i {
    -webkit-box-flex: 0;
    flex: 0 0 auto;
    margin-right: 7px;
}
.u-svg-search {
    width: 15px;
    height: 15px;
    background-image: url(https://xuyouwg.github.io/react-demo/images/search.svg);
}
.m-recom span {
    display: inline-block;
    -webkit-box-flex: 1;
    flex: 1;
    width: 1%;
    padding-right: 10px;
    font-size: 15px;
    line-height: 45px;
    color: #333;
}
.recomitem a{
    display: inline-block;
}